/* <header> */

body{
    margin: 0;
    /* font-family: Arial, Helvetica, sans-serif; */
    background-color: black;
}
.header-container a{
    text-decoration: none;
}
ul{
    list-style-type: none;
}

.header-container{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black;
    /* padding: 10px; */
    position: fixed;
    z-index: 10;
    top: 10px;
    width: 100%;
    
}
.header-left{
    width: 150px;
}

.header-left a{
    color: white;
    font-weight: 600;
    font-size: 20px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.header-mid{
    width: 30%;
    margin-right: 90px;
}
.header-mid a{
    color: rgb(172, 171, 171);
    font-weight: 600;
    font-size: 18px;
}
.header-list-container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    transform: translateY(38%);
}

.header-end-conatiner{
    display: flex;
    justify-content: space-around;
}
.header-list i{
    margin-left: 5px;
}
.header-end{
    width: 20%;
    /* color: gray; */
    font-weight: 600;
}
.header-end a{
    color:  rgb(172, 171, 171);
    font-weight: 600;
    font-size: 17px;
}
.free a{
    color:  rgb(172, 171, 171);;
}

ul li{
    position: relative;
    /* border: 2px solid red; */
    
}
.dropdown a{
    font-size: 14px;
    color: rgb(172, 171, 171);
}

ul li ul.dropdown li{
    display: block;
   transform: translateX(-30px);
   width: 70px;
   padding-bottom: 10px;
}
ul li ul.dropdown{
    width: 90px;
    background-color: rgb(49, 49, 49);
    position: absolute;
    z-index: 10;
    display: none;
    transform: translate(-7px,7px);
    
}



ul li:hover ul.dropdown{
    display: block;
}

.header-list i:hover {
    transform: rotate(180deg);
}
.lang i:hover{
    transform: rotate(180deg);
}

/* banner */

.more-banner{
    margin-top: 60px;
    /* background-image: url(../../Image/rocky_more.webp); */
    height: 650px;
    background-size: cover;
    background-image: linear-gradient(rgba(128, 128, 128, 0), rgba(0, 0, 0, 0.458)), url(../../Image/rocky_more.webp);
    position: relative;
}

.prime{
    color: rgb(36, 148, 246);
    position: absolute;
    right:  5%;
    top: 10px;
    font-weight: 600;
}
.details-more-div{
    /* border: 2px solid red; */
    color: white;
    width: 62%;
    /* margin-left: 60px; */
    padding-left: 70px;
    height: 650px;
    background-image: linear-gradient(to right, rgb(0, 0, 0) , rgba(0, 0, 0, 0));
    position: absolute;
    padding-top: 40px;
    
}
.details-more-div h5{
    font-weight: 700;
}
.head{
    font-size: 48px;
    font-weight: 900;
    /* border: 2px solid red; */
    width: 500px;
    padding-top: 20px;
}
.para{
    padding-top: 20px;
    font-size: 20px;
    font-weight: 700;
}
.time{
    display: flex;
    font-size: 18px;
    font-weight: 600;
}
.time p{
    padding-right: 15px;
}
.color{
    color: rgb(183, 179, 179);
}
.btn {
    color: white;
    font-size: 15px;
    font-weight: 600;
    transform: translateY(-7px);
    /* border: 2px solid red; */
    margin-right: 10px;
    width: 87px;
    background-color: rgba(128, 128, 128, 0.274);
    /* height: 30px; */
}
.genre{
    display: flex;
}
.genre a{
    color: white;
    font-size: 20px;
    font-weight: 600;
    margin-right: 8px;
}
.genre p{
    font-size: 50px;
    padding-right: 10px;
    transform: translateY(-38px);
}
.element-flexx{
    display: flex;
    /* margin-top: -120px; */
}
.element-flexx h6{
    margin-left: 10px;
    transform: translateY(-10px);
}
.btn-three {
    border: none;
    border-radius: 8px;
    margin-right: 10px;
    text-align: start;
    font-weight: 600;
}
.btn-three i{
    padding-left: 5px;
    padding-right: 3px;
}
.sign{
    color: white;
    background-color: rgba(128, 128, 128, 0.296);
}
.signn{
    color: white;
    background-color: rgba(128, 128, 128, 0.296);
    width: 150px;
    height: 65px;
    transform: translateY(8px);
    margin-right: 20px;
}
.icon{
    /* border: 2px solid red; */
    margin-right: 20px;
    font-size: 45px;
    /* width: 60px;
    height: 60px; */
    text-align: center;
    background-color: gray;
    border-radius: 50%;
    transform: translateY(18px);
}
.para-rent{
    font-size: 18px;
    color: rgb(176, 174, 174);
    font-weight: 600;
    padding-top: 20px;
}

.fixed{
    margin-left: 60px;
    color: white;
    /* border: 2px solid red; */
    width: 60%;
    padding-top: 5px;
    display: flex;
    justify-content: center;
    position: absolute;
    left: 20%;
    background-color: rgb(60, 59, 59);
    font-size: 20px;
    font-weight: 600;
    padding-bottom: 5px;
    border-radius: 10px;
    position: sticky;
    position: -webkit-sticky;
    top: 60px; /* required */

}
.related{
    
    border-bottom: 4px solid white;
}
.related:hover{
    color: white;
}
.det{
    color: rgb(207, 206, 206);
    margin-left: 20px;
    
}

